#skills {
  margin: 0 auto;
}
#filter * {
  background-color: #222;
  color: #fff;
  border: 0;
  margin: 10px;
}
#filter button:hover {
  background-color: #222;
  color: #406174;
  border: 0;
  margin: 10px;
}
.skills .skill {
  width: 25%;
  float: left;
  height: 200px;
  overflow: hidden;
  text-align: left;
  cursor: pointer;
  position: relative;
  z-index: 1;
  display: -ms-flexbox;
  display: flex;
  background: #fff;
}
#skills .skill .post-content,
#skills .skill a {
  padding: 20px;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
#skills .skill a {
  z-index: 1000;
  text-indent: 200%;
  white-space: nowrap;
  font-size: 0;
  opacity: 0;
}
#skills .skill h2 {
  font-family: "Montserrat", sans-serif;
  color: #fff;
  font-weight: 300;
  position: absolute;

  bottom: 0;
  padding: 2em 1.2em;
  font-size: 36px;
}
.skill .gr {
  color: #5a5050;
}
.skill:hover .gr {
  color: #fff;
}
#skills .skill h2,
#skills .skill p {
  display: -ms-flexbox;
  display: flex;
  font-family: "Montserrat", sans-serif;
  color: #fff;
  letter-spacing: 1px;
  font-size: 24px;
  padding: 0 10px 0 0;
  width: 100%;
  height: 100%;
  text-align: left;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: left;
      justify-content: left;
  opacity: 0;
  -webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
  transition: opacity 0.35s, -webkit-transform 0.35s;
  -o-transition: opacity 0.35s, transform 0.35s;
  transition: opacity 0.35s, transform 0.35s;
  transition: opacity 0.35s, transform 0.35s, -webkit-transform 0.35s;
  position: absolute;
  z-index: 1;
  margin-left: 16px;
}
#skills .skill:hover p {
  opacity: 1;
}
#skills .skill img {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  opacity: 1;
  -webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
  transition: opacity 0.35s, -webkit-transform 0.35s;
  -o-transition: opacity 0.35s, transform 0.35s;
  transition: opacity 0.35s, transform 0.35s;
  transition: opacity 0.35s, transform 0.35s, -webkit-transform 0.35s;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  min-height: 100%;
  -webkit-transform: scale(1.5);
  -ms-transform: scale(1.5);
      transform: scale(1.5);
  left: 0;
  top: 0;
  left: 0;
}
#skills .skill:hover img {
  opacity: 0.5;
  -webkit-transform: scale(1);
  -ms-transform: scale(1);
      transform: scale(1);
}



